function storeAction({ store, action, payload }) {
  console.log(`Action: ${action}`);

  store.signal('action', { action, namespace: 'player', payload });
}

function switchDevice({ store, device }) {
  // console.log('Switching to device:');
  // console.log(device);

  localStorage.setItem('current_device_key', device.deviceKey);
  store.switch(device);
}

// todo: use from library (ex. dmtJS)
function compareValues(key, order = 'asc') {
  return function innerSort(a, b) {
    if (!a.hasOwnProperty(key) || !b.hasOwnProperty(key)) {
      // property doesn't exist on either object
      return 0;
    }

    const varA = typeof a[key] === 'string' ? a[key].toUpperCase() : a[key];
    const varB = typeof b[key] === 'string' ? b[key].toUpperCase() : b[key];

    let comparison = 0;
    if (varA > varB) {
      comparison = 1;
    } else if (varA < varB) {
      comparison = -1;
    }
    return order === 'desc' ? comparison * -1 : comparison;
  };
}

export { storeAction, switchDevice, compareValues };
